import React, {useState} from "react";


export default function Navbar() {
  {/* Date */}
  const date = new Date().getDate();
  const month = new Date().getMonth();
  const year = new Date().getFullYear();

  {/* Time */}
  const time = new Date().toLocaleTimeString();

  const [currtime, setcurrtime] = useState(time);

  const updatetime = () => {
      const time = new Date().toLocaleTimeString();
      setcurrtime(time);
  }

  setInterval(updatetime, 1000);
 
  return (
    <div className="container">
    <nav class="navbar navbar-expand-md bg-dark">
    <div id="collapse" class="navbar-collapse collapse w-100 order-1 order-md-0 dual-collapse2">
        <ul class="navbar-nav mr-auto">
            
            <li class="nav-item text-white">
            {date}-{month+1}-{year}
            </li>
          
        </ul>
    </div>
    <div class="mx-auto order-0">
        <h3><a class=" text-white navbar-brand mx-auto" href="#">Ravi Kishan</a></h3>
        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target=".dual-collapse2">
            <span class="navbar-toggler-icon"></span>
        </button>
    </div>
    <div id="collapse" class="navbar-collapse collapse w-100 order-3 dual-collapse2">
        <ul class="navbar-nav ml-auto">
            <li class="nav-item text-white">
                {time}
            </li>
           
        </ul>
    </div>
</nav>
    </div>
  );
}
